.page {
  scroll-behavior: smooth;
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-size: 16px;
  font-weight: 400;
  color: #fff;

  &__body {
    background-color: $dark-grey;
    margin: 0;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(100%);
    transition: transform 0.4s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 80px;

    @include onTablet {
      margin-bottom: 120px;
    }

    @include onDesktop {
      margin-bottom: 150px;
    }

    &-title {
      margin: 0 0 48px;
      font-weight: 500;
      font-size: 32px;
      line-height: 32px;
      text-align: center;
      letter-spacing: -1.5px;

      @include onTablet {
        font-size: 48px;
        line-height: 100%;
      }

      @include onDesktop {
        font-size: 64px;
        line-height: 100%;
      }
    }
  }
}
