.details {
  &__detail {
    margin-bottom: 40px;

    @include onTablet {
      margin-bottom: 48px;
    }
  }

}

.detail {
  &__photo-container {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    max-height: 321px;
    overflow: hidden;

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }

    @include onTablet {
      height: 187px;
      padding-bottom: 0;
    }

    @include onDesktop {
      height: 321px;
      padding-bottom: 0;
    }
  }

  &__photos {
    margin-bottom: 16px;
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    transition: all 0.3s;

    &:hover {
      transform: scale(1.1);
    }
  }

  &__description {
    @include onDesktop {
      max-width: 66%;
    }
  }

  &__title {
    @extend .bike__title;
  }

  &__text {
    @extend .bike__text;
    margin: 0;
  }
}
